<template lang="">
  <div>
    <div class="memory_card rounded shadow" @click="clickHandler" :class="{'flipped' : active}">
      <img class="front_face" :src="require('../../assets/img/memory/'+card.img)" :alt="'card ' + card.name">
      <img class="back_face" src="../../assets/img/memory/pattern.jpg" alt="card backface">
    </div>
  </div>
</template>
<script>
export default {
  name: "CardComp",
  data: () => ({

  }),
  props: {
    card: Object,
    active: Boolean
  },
  methods: {
    clickHandler() {
      if (!this.active) {
        this.$emit('click', this.card);
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.memory_card {
  position: relative;
  width: 130px;
  height: 205px;
  margin: 5px;
  position: relative;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, .3);
  transform: scale(1);
  cursor: pointer;
  span {
    display: none;
  }
  @media all and (max-width: 300px) {
    width: 50px;
    height: 75px;
  }

  @media all and (min-width: 300px) and (max-width: 350px) {
    width: 60px;
    height: 90px;
  }

  @media all and (min-width: 350px) and (max-width: 400px) {
    width: 70px;
    height: 105px;
  }

  @media all and (min-width: 400px) and (max-width: 550px) {
    width: 70px;
    height: 105px;
  }

  @media all and (min-width: 550px) and (max-width: 610px) {
    width: 80px;
    height: 120px;
  }

  @media all and (min-width: 610px) and (max-width: 800px) {
    width: 90px;
    height: 135px;
  }

  @media all and (min-width: 800px) and (max-width: 900px) {
    width: 90px;
    height: 135px;
  }

  @media all and (min-width: 900px) and (max-width: 1000px) {
    width: 110px;
    height: 165px;
  }

  @media all and (min-width: 1000px) and (max-width: 1200px) {
    width: 110px;
    height: 165px;
  }

  @media all and (min-width: 1200px) {
    width: 130px;
    height: 175px;
  }

  &:active {
    transform: scale(0.97);
    transition: transform .2s;
  }
}

.front_face {
  transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position: absolute;
  width: 100%;
  height: 100%;
}

.flipped .front_face {
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}

.back_face {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
  width: 100%;
  height: 100%;
}

.flipped .back_face {
  transform: rotateY(90deg);
  transition-delay: 0s;
}
</style>