  created() {
    this.done = false;
    this.getList();
  },
  methods: {
    getList() {
      this.randomList = this.getMeRandomElements(
        this.cards,
        this.numberOfCards
      );
      console.log(this.randomList);
      return this.randomList;
    },
    getMeRandomElements(sourceArray, neededElements) {
      var result = [];
      var result2 = [];
      var results = [];
      var shuffledResults = [];
      while (result.length < neededElements) {
        var item = sourceArray[Math.floor(Math.random() * sourceArray.length)];
        if (!result.includes(item)) {
          result.push(item);
        }
      }
      result2 = structuredClone(result);
      results = [...result, ...result2];
      shuffledResults = results.sort(() => 0.5 - Math.random());
      return shuffledResults;
    },
    handleClick(card) {
      console.log(card);
      this.firstPick ? (this.secondPick = card) : (this.firstPick = card);
      if (this.firstPick && this.secondPick) {
        if (this.firstPick.id === this.secondPick.id) {
          const ind1 = this.randomList.indexOf(card);
          const ind2 = this.randomList.indexOf(this.firstPick);
          this.randomList[ind1].matched = true;
          this.randomList[ind2].matched = true;
          this.resetActive();
        } else {
          this.resetActive();
        }
        this.turns++;
      }
      this.checkIfCompleted();
    },
    resetActive() {
      setTimeout(() => {
        this.firstPick = null;
        this.secondPick = null;
      }, 500);
    },
    checkIfCompleted() {
      if (this.randomList.every((card) => card.matched)) this.done = true;
    },
    resetGame() {
      this.firstPick = null;
      this.secondPick = null;
      this.turns = 0;
      const elements = document.querySelectorAll(".flipped");
      elements.forEach((element) => {
        element.classList.remove("flipped");
      });
      this.randomList.forEach((v) => (v.matched = false));
      this.shuffle(this.randomList);
      // window.location.reload();
    },
    shuffle(data) {
      setTimeout(() => {
        this.done = false;
        for (var a = 0; a < data.length; a++) {
          var x = data[a];
          var y = Math.floor(Math.random() * (a + 1));
          data[a] = data[y];
          data[y] = x;
        }
      }, 500);
    },
  },
